    margin-top: 56%;
    padding-bottom: 46%;
  }
}
.main-container__wrapper span {
  display: none;
}
@media (max-width: 767px) {
  .main-container__wrapper span {
    display: block;
    font-family: "roboto-condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    padding: 12px 30px;
