}
@media (max-width: 767px) {
  .main-footer__scroll details {
    position: relative;
  }
  .main-footer__scroll details::before {
    position: absolute;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
    bottom: 0;
    left: -25px;
  }
}
@media (max-width: 767px) {
  .main-footer__scroll details[open] > summary:before {
    transform: rotate(0deg);
  }
}
.main-footer__scroll summary::-webkit-details-marker {
  display: none;
}
.main-footer__scroll summary::-moz-list-bullet {
  list-style-type: none;
}
.main-footer__scroll summary::marker {
  display: none;
}
.main-footer__scroll summary {
  display: inline-block;
  cursor: pointer;
  font-family: "roboto-condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .main-footer__scroll summary {
    padding-top: 22px;
    padding-bottom: 22px;
    margin-bottom: 0;
    position: relative;
    width: 100%;
  }
  .main-footer__scroll summary::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    right: 0;
    top: 50%;
  }
  .main-footer__scroll summary::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    right: 0;
    top: 50%;
    transform: rotate(90deg);
  }
}
.main-footer__scroll summary:focus {
  outline: 0;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, 0.3);
}
.main-footer__scroll ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .main-footer__scroll ul {
    margin-top: -6px;
  }
}
.main-footer__scroll a {
  font-family: "pt-mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .main-footer__scroll a {
    font-size: 14px;
  }
}
.main-footer__scroll div {
  display: flex;
}
@media (max-width: 1023px) {
  .main-footer__scroll div {
    flex-direction: column;
  }
}
.main-footer__scroll div li {
  max-width: 220px;
  margin-right: 30px;
}
.main-footer__scroll li {
  margin-top: 6px;
}
.main-footer__scroll li:first-of-type {
  margin-top: 0;
}
@media (max-width: 1023px) {
  .main-footer__scroll li {
    margin-bottom: 6px;
    margin-top: 0;
  }
  .main-footer__scroll li:last-of-type {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .main-footer__scroll li:last-of-type {
    padding-bottom: 18px;
  }
}

.social__ok,
.social__pinterest {
  display: inline-block;
  box-sizing: border-box;
}

.social__ok {
  position: relative;
}
.social__ok::after {
  position: absolute;
  content: "";
  width: 4px;
  height: 4px;
  background-color: #f8fdff;
  border-radius: 50px;
  top: 9px;
  left: 13px;
}

.social__pinterest {
  position: relative;
  content: "";
  width: 30px;
  height: 30px;
  background-color: #f8fdff;
  border-radius: 50px;
}
.social__pinterest svg {
  position: absolute;
  z-index: 2;
  background-color: aliceblue;
  border-radius: 50px;
  left: 0;
  top: 0;
}

.link__container {
  margin-right: 96px;
}
@media (max-width: 1023px) {
  .link__container {
    margin-right: 0;
  }
}

@media (max-width: 1023px) {
  .link__container + details {
    margin-right: 13px;
  }
}
@media (max-width: 767px) {
  .link__container + details {
    margin-right: 0;
  }
}

.features__container ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px 30px;
  padding-top: 10%;
  padding-bottom: 2px;
}
@media (max-width: 1023px) {
  .features__container ul {
    padding-top: 76px;
  }
}
@media (max-width: 767px) {
  .features__container ul {
    display: block;
    grid-template-columns: none;
    padding-top: 49px;
  }
}
.features__container h3 {
  padding: 0;
  margin: 0;
  font-family: "roboto-condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: #00b2ff;
  margin-bottom: 15px;
}
@media (max-width: 1023px) {
  .features__container h3 {
    margin-bottom: 7px;
  }
}
@media (max-width: 767px) {
  .features__container h3 {
    font-size: 20px;
  }
}
.features__container .features__wrapper {
  min-height: 300px;
  border: 1px solid #ebebeb;
  outline: 1px;
  box-sizing: border-box;
  position: relative;
}
.features__container .features__wrapper::before {
  position: absolute;
  content: "";
  width: 5px;
  height: 100%;
  background-color: #00b2ff;
}
@media (max-width: 1023px) {
  .features__container .features__wrapper {
    min-height: 360px;
  }
}
@media (max-width: 767px) {
  .features__container .features__wrapper {
    margin-bottom: 31px;
    min-height: 340px;
  }
}
.features__container li {
  padding: 38px 45px 66px;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .features__container li {
    padding: 41px 43px 31px;
  }
}
@media (max-width: 767px) {
  .features__container li {
    padding: 29px 29px 31px;
  }
}
.features__container svg {
  fill: #00b2ff;
  background-color: #ffffff;
  margin-bottom: 14px;
}
@media (max-width: 1023px) {
  .features__container svg {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .features__container svg {
    width: 30px;
    height: 30px;
  }
}
.features__container p {
  padding: 0;
  margin: 0;
  font-family: "pt-mono", "Arial", monospace;
